/* eslint-disable no-confusing-arrow */
// ** React Import
import { useState, useContext, useEffect } from "react"
import Sidebar from "@components/sidebar"
import { selectThemeColors } from "@utils"
import Select from "react-select"
import classnames from "classnames"
import { useForm, Controller } from "react-hook-form"
import { Button, Label, FormText, Form, Input } from "reactstrap"
import store, { addUser } from "@src/views/apps/user/store"
import { useDispatch, useSelector } from "react-redux"
import toast from "react-hot-toast"
import { useTranslation } from "react-i18next"
import { ThemeColors } from "@src/utility/context/ThemeColors"
import InputPasswordToggle from "@components/input-password-toggle"
const defaultValues = {
  email: "",
  password: "",
  phone: "",
  member_of: "",
  id_role: "",
  name: "",
  username: ""
}

const checkIsValid = (data) => {
  return Object.values(data).every((field) =>
    typeof field === "object" || field !== "member_of"
      ? field !== null && field !== ""
      : field.length > 0
  )
}

const SidebarNewUsers = ({ open, toggleSidebar }) => {
  // ** Hooks
  const { t } = useTranslation()

  // ** Theme Colors
  const { colors } = useContext(ThemeColors)

  // ** States
  const [data, setData] = useState({})
  const [idRole, setIdRole] = useState(3)
  const [idUnit, setIdUnit] = useState([])
  const store = useSelector((state) => state.users)
  const storeRole = useSelector((state) => state.roles)
  const storeUnit = useSelector((state) => state.units)
  // ** Store Vars
  const dispatch = useDispatch()

  // ** Vars
  const {
    control,
    setValue,
    setError,
    handleSubmit,
    formState: { errors }
  } = useForm({ defaultValues })

  // ** Function to handle form submit
  const onSubmit = (data) => {
    // console.log(data)
    data["member_of"] = idUnit !== null ? idUnit.map((item) => item.value) : []
    data["id_role"] = idRole !== null ? idRole : null
    setData(data)
    // console.log(data)
    if (checkIsValid(data)) {
      dispatch(
        addUser({
          id_role: data?.id_role,
          avatar: "",
          status: 1,
          email: data.email,
          member_of: data?.member_of,
          phone: data.phone,
          name: data.name,
          username: data.username,
          password: data.password
        })
      ).then((res) => {
        if (res.meta.requestStatus === "fulfilled") {
          toggleSidebar()
          toast.success(`${t("Successfully Create Data")}`, {
            style: {
              padding: "16px",
              color: colors.success.main,
              border: `1px solid ${colors.success.main}`
            },
            iconTheme: {
              primary: colors.success.main
            }
          })
        } else if (res.meta.requestStatus === "rejected") {
          // console.log(store.errorAddEdit)
          let message = ""
          if (res.payload.errors) {
            message = Object.values(res.payload.errors)
          } else if (res.payload.error_reason) {
            message = res.payload.message
          }
          toast.error(`${t(message)}`, {
            style: {
              padding: "16px",
              color: colors.danger.main,
              border: `1px solid ${colors.danger.main}`
            },
            iconTheme: {
              primary: colors.danger.main
            }
          })
        }
      })
    } else {
      for (const key in data) {
        if (data[key] !== null && data[key].length === 0) {
          setError(key, {
            type: "manual"
          })
        }
      }
    }
  }

  const handleSidebarClosed = () => {
    for (const key in defaultValues) {
      setValue(key, "")
    }
    // setRole(null)
  }

  useEffect(() => {
    const selectedUnit = storeUnit.data.map((item) => ({
      value: item.id,
      label: item.name
    }))
    // console.log(selectedUnit[0])
    setIdUnit([selectedUnit[0]])
  }, [dispatch, storeUnit.data.length])

  return (
    <Sidebar
      size="lg"
      open={open}
      title={t("New User")}
      headerClassName="mb-1"
      contentClassName="pt-0"
      toggleSidebar={toggleSidebar}
      onClosed={handleSidebarClosed}
    >
      <Form onSubmit={handleSubmit(onSubmit)}>
        <div className="mb-1">
          <Label className="form-label" for="name">
            {t("Full Name")}
            <span className="text-danger">*</span>
          </Label>
          <Controller
            defaultValue=""
            control={control}
            rules={{
              required: true,
              pattern: {
                value: /^[a-zA-Z\s]*$/,
                message: t("Invalid name, you can only use letters")
              }
            }}
            id="name"
            name="name"
            render={({ field }) => (
              <Input
                {...field}
                id="name"
                placeholder="John"
                invalid={errors.name && true}
              />
            )}
          />
          {errors.name && (
            <FormText color="danger">{t(errors.name.message)}</FormText>
          )}
        </div>

        <div className="mb-1">
          <Label className="form-label" for="email">
            {t("Email")} <span className="text-danger">*</span>
          </Label>
          <Controller
            name="email"
            control={control}
            rules={{
              required: {
                value: true,
                message: t("You need to specify a valid email address")
              },
              pattern: {
                value: /^\S+@\S+$/i,
                message: t("Not valid email adreess.")
              }
            }}
            render={({ field }) => (
              <Input
                type="email"
                id="email"
                placeholder="john.doe@example.com"
                invalid={errors.email && true}
                {...field}
              />
            )}
          />
          <FormText color="muted">
            {t("You can use letters, numbers & periods")}
          </FormText>
        </div>

        <div className="mb-1">
          <Label className="form-label" for="username">
            {t("Username")}
            <span className="text-danger">*</span>
          </Label>
          <Controller
            defaultValue=""
            control={control}
            rules={{
              required: true,
              minLength: {
                value: 4,
                message: t("minimum number of character is 4")
              },
              maxLength: {
                value: 15,
                message: t("maximum number of character is 15")
              },
              pattern: {
                value:
                  /^[a-zA-Z0-9]+([a-zA-Z0-9](_|-| )[a-zA-Z0-9])*[a-zA-Z0-9]+$/,
                message: t(
                  "You can use letters and numbers without white space"
                )
              }
            }}
            id="username"
            name="username"
            render={({ field }) => (
              <Input
                {...field}
                id="username"
                placeholder="john.doe.007"
                invalid={errors.username && true}
              />
            )}
          />
          {errors.username && (
            <FormText color="danger">{t(errors.username.message)}</FormText>
          )}
        </div>

        <div className="mb-1">
          <div className="d-flex justify-content-between">
            <Label className="form-label" for="login-password">
              {t("Password")} <span className="text-danger">*</span>
            </Label>
          </div>
          <Controller
            defaultValue=""
            id="password"
            name="password"
            control={control}
            rules={{
              required: {
                value: true,
                message: t("Password is required")
              },
              minLength: {
                value: 8,
                message: t("Password must have at least 8 characters")
              }
            }}
            render={({ field }) => (
              <InputPasswordToggle
                className="input-group-merge"
                invalid={errors.password && true}
                {...field}
              />
            )}
          />
          {errors.password && (
            <FormText color="danger">{t(errors.password.message)}</FormText>
          )}
        </div>

        <div className="mb-1">
          <Label className="form-label" for="phone">
            {t("Mobile Phone Number")}
          </Label>
          <Controller
            defaultValue=""
            control={control}
            rules={{
              required: false,
              pattern: {
                value: /^[0-9+-]+$/,
                message: "This is not a valid mobile phone, try again!"
              },
              minLength: {
                value: 6,
                message: "This number is too short."
              },
              maxLength: {
                value: 14,
                message: "...And now it's too long."
              }
            }}
            id="phone"
            name="phone"
            render={({ field }) => (
              <Input {...field} id="phone" placeholder="+62 813 940 10XX X" />
            )}
          />
          {errors.phone && (
            <FormText color="danger">{t(errors.phone.message)}</FormText>
          )}
        </div>
        <div className="mb-1">
          <Label className="form-label" for="id_unit">
            {t("Unit")}
          </Label>
          <Select
            id="id_unit"
            isClearable={false}
            theme={selectThemeColors}
            closeMenuOnSelect={false}
            // defaultValue={null}
            onChange={(data) => {
              setIdUnit(data)
            }}
            defaultValue={
              storeUnit.data.map((item) => ({
                value: item.id,
                label: item.name
              }))[0]
            }
            isMulti
            options={storeUnit.data.map((item) => ({
              value: item.id,
              label: item.name
            }))}
            className="react-select"
            classNamePrefix="select"
          />
        </div>
        <div className="mb-1">
          <Label for="role-select">{t("Role")}</Label>
          <Select
            id="id_role"
            isClearable={false}
            options={storeRole.data.map((item) => ({
              value: item.id,
              label: item.name
            }))}
            defaultValue={{
              label: "editor",
              vaue: 3
            }}
            classNamePrefix="select"
            className={classnames("react-select", {
              "is-invalid": idRole === null
            })}
            theme={selectThemeColors}
            onChange={(data) => {
              // console.log(data.value)
              setIdRole(data.value)
            }}
          />
        </div>

        <Button type="submit" className="me-1" color="primary">
          {t("Submit")}
        </Button>
        <Button type="reset" color="secondary" outline onClick={toggleSidebar}>
          {t("Cancel")}
        </Button>
      </Form>
    </Sidebar>
  )
}

export default SidebarNewUsers
