import { Fragment, useState, useEffect, useContext, useMemo } from "react"
import { Link } from "react-router-dom"
import { useTranslation } from "react-i18next"
import { AbilityContext } from "@src/utility/context/Can"
import Sidebar from "@src/views/apps/user/Sidebar"
import ModalAddEdit from "@src/views/apps/user/modal"
import { neutralizeBack, revivalBack } from "@src/utility/Utils"
import Avatar from "@components/avatar"
import { getData, getUser, deleteUser } from "@src/views/apps/user/store"
import { getData as getDataRole } from "@src/views/apps/roles-permissions/store/role"
import { useDispatch, useSelector } from "react-redux"
import Select from "react-select"
import ReactPaginate from "react-paginate"
import DataTable from "react-data-table-component"
import {
  ChevronDown,
  Share,
  Printer,
  FileText,
  File,
  Grid,
  Copy,
  Slack,
  User,
  Settings,
  Database,
  Edit2,
  MoreVertical,
  Trash2,
  Archive,
  Eye
} from "react-feather"
import UILoader from "@components/ui-loader"
import Spinner from "@components/spinner/Loading-spinner"
import { selectThemeColors } from "@utils"
import { ThemeColors } from "@src/utility/context/ThemeColors"
import GlobalUnitDropdown from "@src/shared/GlobalUnitDropdown"
import toast from "react-hot-toast"
import {
  Row,
  Col,
  Card,
  Input,
  Label,
  Button,
  CardBody,
  CardTitle,
  CardHeader,
  DropdownMenu,
  DropdownItem,
  DropdownToggle,
  UncontrolledDropdown,
  Badge,
  CardText
} from "reactstrap"
import "@styles/react/libs/react-select/_react-select.scss"
import "@styles/react/libs/tables/react-dataTable-component.scss"
import Swal from "sweetalert2"
import withReactContent from "sweetalert2-react-content"
import { Skeleton } from "@mui/material"
import Moment from "moment"
const MySwal = withReactContent(Swal)

// ** Table Header
const CustomHeader = ({
  store,
  toggleSidebar,
  handlePerPage,
  rowsPerPage,
  handleFilter,
  searchTerm
}) => {
  const { t } = useTranslation()
  const ability = useContext(AbilityContext)

  // ** Converts table to CSV
  function convertArrayOfObjectsToCSV(array) {
    let result

    const columnDelimiter = ","
    const lineDelimiter = "\n"
    const keys = Object.keys(store.data[0])

    result = ""
    result += keys.join(columnDelimiter)
    result += lineDelimiter

    array.forEach((item) => {
      let ctr = 0
      keys.forEach((key) => {
        if (ctr > 0) result += columnDelimiter

        result += item[key]

        ctr++
      })
      result += lineDelimiter
    })

    return result
  }

  // ** Downloads CSV
  function downloadCSV(array) {
    const link = document.createElement("a")
    let csv = convertArrayOfObjectsToCSV(array)
    if (csv === null) return

    const filename = "export.csv"

    if (!csv.match(/^data:text\/csv/i)) {
      csv = `data:text/csv;charset=utf-8,${csv}`
    }

    link.setAttribute("href", encodeURI(csv))
    link.setAttribute("download", filename)
    link.click()
  }

  return (
    <div className="invoice-list-table-header w-100 me-1 ms-50 mt-2 mb-75">
      <Row>
        <Col xl="6" className="d-flex align-items-center p-0">
          <div className="d-flex align-items-center w-100">
            <label htmlFor="rows-per-page">{t("Show")}</label>
            <Input
              className="mx-50"
              type="select"
              id="rows-per-page"
              value={rowsPerPage}
              onChange={handlePerPage}
              style={{ width: "5rem" }}
            >
              <option value="10">10</option>
              <option value="25">25</option>
              <option value="50">50</option>
            </Input>
            <label htmlFor="rows-per-page">{t("Entries")}</label>
          </div>
        </Col>
        <Col
          xl="6"
          className="d-flex align-items-sm-center justify-content-xl-end justify-content-start flex-xl-nowrap flex-wrap flex-sm-row flex-column pe-xl-1 p-0 mt-xl-0 mt-1"
        >
          <div className="d-flex align-items-center mb-sm-0 mb-1 me-1">
            <label className="mb-0" htmlFor="search-invoice">
              {t("Search")}:
            </label>
            <Input
              id="search-invoice"
              className="ms-50 w-100"
              type="text"
              value={searchTerm}
              onChange={(e) => handleFilter(e.target.value)}
            />
          </div>

          <div className="d-flex align-items-center table-header-actions">
            <UncontrolledDropdown className="me-1">
              <DropdownToggle color="secondary" caret outline>
                <Share className="font-small-4 me-50" />
                <span className="align-middle">{t("Export")}</span>
              </DropdownToggle>
              <DropdownMenu>
                <DropdownItem className="w-100">
                  <Printer className="font-small-4 me-50" />
                  <span className="align-middle">Print</span>
                </DropdownItem>
                <DropdownItem
                  className="w-100"
                  onClick={() => downloadCSV(store.data)}
                >
                  <FileText className="font-small-4 me-50" />
                  <span className="align-middle">CSV</span>
                </DropdownItem>
                <DropdownItem className="w-100">
                  <Grid className="font-small-4 me-50" />
                  <span className="align-middle">Excel</span>
                </DropdownItem>
                <DropdownItem className="w-100">
                  <File className="font-small-4 me-50" />
                  <span className="align-middle">PDF</span>
                </DropdownItem>
                <DropdownItem className="w-100">
                  <Copy className="font-small-4 me-50" />
                  <span className="align-middle">Copy</span>
                </DropdownItem>
              </DropdownMenu>
            </UncontrolledDropdown>

            {ability.can(
              "post",
              sessionStorage.getItem("current_page_perm")
            ) ? (
              <Button
                className="add-new-user"
                color="primary"
                onClick={toggleSidebar}
              >
                {t("Add New")} {t(store.crudTitle)}
              </Button>
            ) : null}
          </div>
        </Col>
      </Row>
    </div>
  )
}

const UsersList = () => {
  // ** Store Vars
  const dispatch = useDispatch()
  const store = useSelector((state) => state.users)
  const storeUnit = useSelector((state) => state.units)
  const storeRole = useSelector((state) => state.roles)

  // ** Hooks
  const { t } = useTranslation()
  const ability = useContext(AbilityContext)
  // ** Theme Colors
  const { colors } = useContext(ThemeColors)

  // ** States
  const [sort_dir, setSortDir] = useState("desc")
  const [searchTerm, setSearchTerm] = useState("")
  const [currentPage, setCurrentPage] = useState(store.params?.page ?? 1)
  const [sort, setSort] = useState("id")
  const [rowsPerPage, setRowsPerPage] = useState(store.params?.length ?? 10)
  const [sidebarOpen, setSidebarOpen] = useState(false)
  const [currentRole, setCurrentRole] = useState({
    value: "",
    label: `${t("Select")} ${t("Role")}`
  })
  const [currentUnit, setCurrentUnit] = useState({
    value: "",
    label: `${t("Select")} ${t("Unit")}`
  })
  const [currentStatus, setCurrentStatus] = useState({
    value: "",
    label: `${t("Select")} Status`,
    number: 0
  })
  const [roleOptions, setRoleOptions] = useState([])
  const [loading, setLoading] = useState(false)
  const [showModal, setShowModal] = useState(false)

  // ** Function to toggle sidebar
  const handleCloseModal = () => {
    setSidebarOpen(!sidebarOpen)
    revivalBack()
  }

  const toggleSidebar = () => {
    setSidebarOpen(!sidebarOpen)
    neutralizeBack(handleCloseModal)
  }

  // ** Get data on mount
  useEffect(() => {
    // console.log(store.params)
    if (storeRole?.data.length === 0 && !storeRole.isLoading) {
      dispatch(getDataRole()).then((x) => {
        if (x.data) {
          const roleOptionss = x.payload.data.map((item) => ({
            value: item.id,
            label: item.name
          }))
          roleOptionss.unshift({
            value: "",
            label: `${t("Select")} ${t("Role")}`
          })
          setRoleOptions(roleOptionss)
        }
      })
    } else {
      const roleOptionss = storeRole.data.map((item) => ({
        value: item.id,
        label: item.name
      }))
      roleOptionss.unshift({
        value: "",
        label: `${t("Select")} ${t("Role")}`
      })
      setRoleOptions(roleOptionss)
    }

    if (store.data.length === 0 && !store.isLoading) {
      dispatch(
        getData({
          sort_dir,
          sort,
          search: searchTerm,
          page: currentPage,
          length: rowsPerPage,
          filter_role: currentRole.value,
          filter_status: currentStatus.value,
          filter_unit: currentUnit.value
        })
      )
    }
  }, [
    dispatch,
    store.data.length,
    storeUnit.data.length,
    storeRole.data.length,
    sort_dir,
    sort,
    currentPage
  ])

  const statusObj = {
    pending: "light-warning",
    active: "light-success",
    inactive: "light-secondary"
  }

  // ** Renders Client Columns
  const renderClient = (row) => {
    if (row.avatar) {
      return (
        <Avatar className="me-1" img={row.avatar} width="32" height="32" />
      )
    } else {
      return (
        <Avatar
          initials
          className="me-1"
          color={row.avatarColor || "light-primary"}
          content={row.name || "John Doe"}
        />
      )
    }
  }

  // ** Renders Role Columns
  const renderRole = (row) => {
    const roleObj = {
      public: {
        class: "text-primary",
        icon: User
      },
      viewer: {
        class: "text-success",
        icon: Eye
      },
      editor: {
        class: "text-info",
        icon: Edit2
      },
      admin: {
        class: "text-warning",
        icon: Settings
      },
      super_user: {
        class: "text-danger",
        icon: Slack
      }
    }

    const Icon = roleObj[row.role_name] ? roleObj[row.role_name].icon : Edit2

    return (
      <span className="text-truncate text-capitalize align-middle">
        <Icon
          size={18}
          className={`${
            roleObj[row.role_name] ? roleObj[row.role_name].class : ""
          } me-50`}
        />
        {row.role_name}
      </span>
    )
  }

  const columns = [
    {
      name: t("User"),
      sortable: true,
      minWidth: "400px",
      sortField: "name",
      selector: (row) => row.name,
      cell: (row) => (
        <div className="d-flex justify-content-left align-items-center">
          {renderClient(row)}
          <div className="d-flex flex-column">
            <Link
              to={`/apps/user/view/${row.id}`}
              className="user_name text-truncate text-body"
              // onClick={() => store.dispatch(getUser(row.id))}
            >
              <span className="fw-bolder">{row.name}</span>
            </Link>
            <small className="text-truncate text-muted mb-0">{row.email}</small>
          </div>
        </div>
      )
    },
    {
      name: t("Role"),
      sortable: true,
      minWidth: "200px",
      sortField: "role_name",
      selector: (row) => row.role_name,
      cell: (row) => renderRole(row)
    },
    // {
    //   name: "Unit",
    //   minWidth: "138px",
    //   sortable: true,
    //   sortField: "unit",
    //   selector: (row) => row.currentPlan,
    //   cell: (row) => <span className="text-capitalize">{row.currentPlan}</span>
    // },
    {
      name: t("Joined Date"),
      minWidth: "230px",
      sortable: true,
      sortField: "joined_date",
      selector: (row) => row.billing,
      cell: (row) => (
        <span className="text-capitalize">
          {Moment(row.joined_date).format("DD MMM YYYY")}
        </span>
      )
    },
    {
      name: t("Status"),
      minWidth: "120px",
      sortable: true,
      sortField: "status",
      selector: (row) => row.is_active,
      cell: (row) => (
        <Badge
          className="text-capitalize"
          color={statusObj[row.is_active ? "active" : "inactive"]}
          pill
        >
          {row.is_active ? t("active") : t("inactive")}
        </Badge>
      )
    },
    {
      name: t("Actions"),
      width: "130px",
      cell: (row) => (
        <div className="column-action text-center">
          <UncontrolledDropdown>
            <DropdownToggle tag="span">
              <MoreVertical size={14} className="cursor-pointer" />
            </DropdownToggle>
            <DropdownMenu end>
              <DropdownItem
                tag={Link}
                className="w-100"
                to={`/apps/user/view/${row.id}`}
                onClick={() => store.dispatch(getUser(row.id))}
              >
                <FileText size={14} className="me-50" />
                <span className="align-middle">{t("Details")}</span>
              </DropdownItem>

              {ability.can(
                "update",
                sessionStorage.getItem("current_page_perm")
              ) && (
                <DropdownItem
                  tag="a"
                  href="/"
                  className="w-100"
                  onClick={(e) => {
                    e.preventDefault()
                    handleEdit(row.id)
                  }}
                >
                  <Archive size={14} className="me-50" />
                  <span className="align-middle">{t("Edit")}</span>
                </DropdownItem>
              )}

              {ability.can(
                "delete",
                sessionStorage.getItem("current_page_perm")
              ) && (
                <DropdownItem
                  tag="a"
                  href="/"
                  className="w-100"
                  onClick={(e) => {
                    e.preventDefault()
                    handleDelete(row.id)
                  }}
                >
                  <Trash2 size={14} className="me-50" />
                  <span className="align-middle">{t("Delete")}</span>
                </DropdownItem>
              )}
            </DropdownMenu>
          </UncontrolledDropdown>
        </div>
      )
    }
  ]

  const tableColumns = useMemo(
    () =>
      store.isLoading
        ? columns.map((column) => ({
            name: column.name,
            sortable: false,
            minWidth: column.minWidth,
            width: column.width,
            cell: (row) => (
              <Skeleton
                sx={{ bgcolor: colors.dark.light }}
                animation="wave"
                className={column.name === "Actions" ? "ms-2" : ""}
                width={column.name === "Actions" ? 10 : "100%"}
                height={40}
              />
            )
          }))
        : columns.map((column) => ({
            name: column.name,
            sortable: column.sortable,
            minWidth: column.minWidth,
            width: column.width,
            sortField: column.sortField,
            selector: column.selector,
            cell: column.cell
          })),
    [store.isLoading, columns]
  )

  const statusOptions = [
    { value: "", label: `${t("Select")} Status`, number: 0 },
    { value: "pending", label: `${t("Pending")}`, number: 1 },
    { value: "active", label: `${t("Active")}`, number: 2 },
    { value: "inactive", label: `${t("Inactive")}`, number: 3 }
  ]

  const handleChangeUnit = (value) => {
    setCurrentUnit(value)
    dispatch(
      getData({
        sort_dir,
        sort,
        search: searchTerm,
        page: currentPage,
        length: rowsPerPage,
        filter_role: currentRole.value,
        filter_unit: value.value,
        filter_status: currentStatus.value
      })
    )
  }

  const Loader = () => {
    return (
      <Fragment>
        <Spinner />
        <CardText className="mb-0 mt-1">{t("Please Wait")}...</CardText>
      </Fragment>
    )
  }

  const handleDelete = (id) => {
    return MySwal.fire({
      title: `${t("Are you sure?")}`,
      text: `${t("You won't be able to revert")} ${t("user")}!`,
      icon: "warning",
      showCancelButton: true,
      confirmButtonText: `${t("Yes, Delete User!")}`,
      cancelButtonText: `${t("Cancel")}`,
      customClass: {
        confirmButton: "btn btn-primary",
        cancelButton: "btn btn-outline-danger ms-1"
      },
      buttonsStyling: false
    }).then(function (result) {
      if (result.value) {
        setLoading(true)
        dispatch(deleteUser(id)).then((res) => {
          // console.log(res)
          if (res.meta.requestStatus === "fulfilled") {
            setLoading(false)
            toast.success(`${t("Successfully deleted Data")}`, {
              style: {
                padding: "16px",
                color: colors.success.main,
                border: `1px solid ${colors.success.main}`
              },
              iconTheme: {
                primary: colors.success.main
              }
            })
          } else if (res.meta.requestStatus === "rejected") {
            setLoading(false)
            toast.error(res.payload.message, {
              style: {
                padding: "16px",
                color: colors.danger.main,
                border: `1px solid ${colors.danger.main}`
              },
              iconTheme: {
                primary: colors.danger.main
              }
            })
          }
        })
      }
    })
  }

  const handleEdit = (id) => {
    setLoading(true)
    if (
      !store.selectedUser ||
      (store.selectedUser?.id !== parseInt(id) && !store.isLoadingDetail)
    ) {
      dispatch(getUser(id)).then((res) => {
        setShowModal(true)
        setLoading(false)
      })
    } else {
      setLoading(false)
      setShowModal(true)
    }
  }

  // ** Function in get data on page change
  const handlePagination = (page) => {
    dispatch(
      getData({
        sort_dir,
        sort,
        search: searchTerm,
        length: rowsPerPage,
        page: page.selected + 1,
        filter_role: currentRole.value,
        filter_status: currentStatus.value,
        filter_unit: currentUnit.value
      })
    )
    setCurrentPage(page.selected + 1)
  }

  // ** Function in get data on rows per page
  const handlePerPage = (e) => {
    const value = parseInt(e.currentTarget.value)
    dispatch(
      getData({
        sort_dir,
        sort,
        search: searchTerm,
        length: value,
        page: currentPage,
        filter_role: currentRole.value,
        filter_unit: currentUnit.value,
        filter_status: currentStatus.value
      })
    )
    setRowsPerPage(value)
  }

  // ** Function in get data on search searchuery change
  const handleFilter = (val) => {
    setSearchTerm(val)
    dispatch(
      getData({
        sort_dir,
        search: val,
        sort,
        page: currentPage,
        length: rowsPerPage,
        filter_role: currentRole.value,
        filter_status: currentStatus.value,
        filter_unit: currentUnit.value
      })
    )
  }

  // ** Custom Pagination
  const CustomPagination = () => {
    const count = Number(Math.ceil(store.total / rowsPerPage))

    return (
      <ReactPaginate
        previousLabel={""}
        nextLabel={""}
        pageCount={count || 1}
        activeClassName="active"
        forcePage={currentPage !== 0 ? currentPage - 1 : 0}
        onPageChange={(page) => handlePagination(page)}
        pageClassName={"page-item"}
        nextLinkClassName={"page-link"}
        nextClassName={"page-item next"}
        previousClassName={"page-item prev"}
        previousLinkClassName={"page-link"}
        pageLinkClassName={"page-link"}
        containerClassName={
          "pagination react-paginate justify-content-end my-2 pe-1"
        }
      />
    )
  }

  const handleSort = (column, sortDirection) => {
    setSortDir(sortDirection)
    setSort(column.sortField)
    dispatch(
      getData({
        sort_dir,
        sort,
        search: searchTerm,
        page: currentPage,
        length: rowsPerPage,
        filter_role: currentRole.value,
        filter_status: currentStatus.value,
        filter_unit: currentUnit.value
      })
    )
  }

  // ** Table data to render
  const dataToRender = () => {
    const filters = {
      filter_role: currentRole.value,
      filter_unit: currentUnit.value,
      filter_status: currentStatus.value,
      search: searchTerm
    }
    // console.log(tableData)
    // console.log(tableColumns)
    const isFiltered = Object.keys(filters).some(function (k) {
      return filters[k].length > 0
    })
    if (!store.isLoading) {
      if (store.data.length > 0) {
        return store.data
      } else if (store.data.length === 0 && isFiltered) {
        return []
      } else {
        // console.log(store.allData)
        // return store.allData.slice(0, rowsPerPage)
      }
    } else {
      return Array(10).fill({})
    }
  }

  return (
    <Fragment>
      <UILoader blocking={loading} loader={<Loader />}>
        <Card>
          {/* <CardHeader>
            <CardTitle tag="h4">{t("Filters")}</CardTitle>
          </CardHeader> */}
          <CardBody>
            <Row>
              <Col>
                <Label for="role-select">{t("Role")}</Label>
                <Select
                  isClearable={false}
                  value={currentRole}
                  options={roleOptions}
                  className="react-select"
                  classNamePrefix="select"
                  theme={selectThemeColors}
                  onChange={(data) => {
                    setCurrentRole(data)
                    dispatch(
                      getData({
                        sort_dir,
                        sort,
                        search: searchTerm,
                        filter_role: data.value,
                        page: currentPage,
                        length: rowsPerPage,
                        filter_status: currentStatus.value,
                        filter_unit: currentUnit.value
                      })
                    )
                  }}
                />
              </Col>
              <Col>
                <Label for="status-select">Status</Label>
                <Select
                  theme={selectThemeColors}
                  isClearable={false}
                  className="react-select"
                  classNamePrefix="select"
                  options={statusOptions}
                  value={currentStatus}
                  onChange={(data) => {
                    setCurrentStatus(data)
                    dispatch(
                      getData({
                        sort_dir,
                        sort,
                        search: searchTerm,
                        page: currentPage,
                        filter_status: data.value,
                        length: rowsPerPage,
                        filter_role: currentRole.value,
                        filter_unit: currentUnit.value
                      })
                    )
                  }}
                />
              </Col>
              <GlobalUnitDropdown
                parentChange={handleChangeUnit}
                colMode={true}
              />
            </Row>
          </CardBody>
        </Card>

        <Card className="overflow-hidden">
          <div className="react-dataTable">
            <DataTable
              noHeader
              subHeader
              sortServer
              pagination
              responsive
              paginationServer
              style={{ "min-height": "400px" }}
              columns={tableColumns}
              onSort={handleSort}
              sortIcon={<ChevronDown />}
              className="react-dataTable"
              paginationComponent={CustomPagination}
              data={dataToRender()}
              subHeaderComponent={
                <CustomHeader
                  store={store}
                  searchTerm={searchTerm}
                  rowsPerPage={rowsPerPage}
                  handleFilter={handleFilter}
                  handlePerPage={handlePerPage}
                  toggleSidebar={toggleSidebar}
                />
              }
            />
          </div>
        </Card>

        <Sidebar open={sidebarOpen} toggleSidebar={toggleSidebar} />
      </UILoader>

      {store.selectedUser !== null && (
        <ModalAddEdit
          show={showModal}
          setShow={setShowModal}
          detailPage={false}
          // idRow={store.selectedUser.id}
        />
      )}
    </Fragment>
  )
}

export default UsersList
